@import 'common.scss';

@media (max-width: 1050px) {

    .content {
        .page {

            .price {
                .price__container {
                    .price__title {
                        .price__title_items {
                            .price__title_item {
                                &:first-child {
                                    flex: 1;
                                }
                                &:nth-child(2) {
                                    flex: 0 0 96px
                                }
                                &:nth-child(3) {
                                    flex: 0 0 105px
                                }
                                &:nth-child(4) {
                                    flex: 0 0 125px
                                }
                            }
                        }            
                    }
                    .price__items {
                        .price__item {
                            .price__item_item {
                                &:first-child {
                                    flex: 1;
                                }
                                &:nth-child(2) {
                                    flex: 0 0 96px
                                }
                                &:nth-child(3) {
                                    flex: 0 0 105px
                                }
                                &:nth-child(4) {
                                    flex: 0 0 125px
                                }
                            }
                        }
                    }
                    .price__info {
                        padding: 24px 0 16px 0;
                    }
            
                }
            }
            
        }
    }
}

