@import '~normalize.css'; 


$menu-background-color: #FFF;
$main-background-color: #F8F8F8;
$border-color: rgb(234, 234, 234);
$pink: rgb(255, 62, 121);
$black-text: rgb(27, 28, 33);
$grey-text: rgb(126, 126, 130);

a {
    cursor: pointer;
    text-decoration: none;
}

.line {
    width: 2px;
    height: 32px;
    background: rgb(242, 242, 242);
}

.logo {
    a {
        div {
            background-image: url(../assets/images/logo.png);
            background-size: auto;
            background-repeat: no-repeat;
            width: 97px;
            height: 51px;
            transition: 0.5s;
            &:hover {
                filter: drop-shadow(0px 0px 1px #80949F);
                scale: 1.05;
            }
        }
    }
}

.button-icon {
    a {
        div {
            width: 40px;
            height: 40px;
            background-size: auto;
            background-repeat: no-repeat;
            transition: 0.5s;
        }
        :hover {
            transition: 0.5s;
            filter: drop-shadow(0px 0px 5px $pink);
            scale: 1.1;
        }
    }
}

.container {
    padding: 24px 32px;
    border-bottom: 1px solid  rgb(217, 255, 245);
}

.button-more {
    font-family: "TT Lakes Medium", sans-serif;
    padding-top: 24px;
    display: flex;
    align-items: center;
    transition: 0.5s;
    color: $black-text;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: -0.2px;
    .button-more_img {
        background-image: url(../assets/images/icon/icon_arrow.png);
        background-size: auto;
        background-repeat: no-repeat;
        background-position: center;
        width: 8px;
        height: 12px;
        padding: 16px;
    }
    &:hover {
        opacity: 0.5;
    }
}

.title {
    font-family: "TTLakes-Regular", sans-serif;
    color: $grey-text;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    text-transform: uppercase;
    background: linear-gradient(270.00deg, rgb(255, 255, 255), rgb(248, 248, 248) 98.614%);
    .title__container {
        margin: 0 auto;
        max-width: 1120px;
        .title_info {
            padding: 16px 32px;
        }
    }
}


.brands__item__logo {
    background-size: auto;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center left
}



.swiper-pagination,
.swiper-pagination3,
.swiper-pagination2 {
    display: flex;
    justify-content: center;
    position: relative;
    padding-top: 16px;
}

.swiper-pagination-bullet {
    background: rgb(221, 221, 221);
    width: 12px;
    height: 12px;
    margin: 0 6px;
}

.swiper-pagination-bullet-active {
    background: rgb(181, 182, 188);   
}

.no-scroll {
    overflow: hidden;
}



.menu-open {
    .menu__container-modal {
        background-color: $menu-background-color;  
        box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2);
    }
}


.dn {
    display: none;
}


.menu-opacity,
.page-opacity,
.header-opacity {
    pointer-events: none;
    opacity: 0.1;
    
}
