@import 'common.scss';
body {
    background-color: $main-background-color;
    font-family: 'TT Lakes Medium', sans-serif;
    // overflow: hidden;
    position: relative;
}





.header {
    opacity: 0; /* Скрываем элемент */
    visibility: hidden; /* Делаем элемент невидимым для взаимодействия */
    height: 0; /* Убираем занимаемое место */
    transform: translateY(-100%); /* Сдвигаем элемент вверх */
    transition: opacity 0.5s ease, transform 0.5s ease, height 0.5s ease; /* Плавные переходы */

    .header__menu {
        display: flex;
        justify-content: space-between;
        padding: 24px 24px;
        background-color: $menu-background-color;
        border-bottom: 1px solid rgb(217, 255, 245);
        .header__burger-logo {
            display: flex;
            align-items: center;
            gap: 20px;
            .header__burger-logo {
                a {
                    div {
                        background-image: url(../assets/images/icon/ic_burger.png);
                    }
                }
            }
        }
        .header__action {
            display: flex;
            align-items: center;
            gap: 20px;
            .header__buttons {
                display: flex;
                gap: 16px;
                .order-call {
                    a {
                        div {
                            background-image: url(../assets/images/icon/ic_phone.png);
                        }
                    }
                    &:hover {
                        a {
                            div {
                                &:hover{
                                    transform: rotate(-30deg);
                                }
                            }
                        }
                    }
                }
                .feedback {
                    a {
                        div {
                            background-image: url(../assets/images/icon/ic_chat.png);
                        }
                    }
                    
                }
                .pfofile {
                    a {
                        div {
                            background-image: url(../assets/images/icon/ic_profile.png);
                        }
                    }
                }
            }
            .header__button__status {
                display: flex;
                gap: 16px;
                .repair {
                    a {
                        div {
                            background-image: url(..//assets//images/icon/ic_repair.png)
                        }
                    }
                }
                .checkstatus {
                    a {
                        div {
                            background-image: url(..//assets//images/icon/ic_checkstatus.png)
                        }
                    }
                }
            }
        }
    }
}

.content {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    .menu {
        z-index: 100;
        opacity: 1; /* Делаем элемент видимым */
        visibility: visible; /* Делаем элемент видимым для взаимодействия */
        height: auto; /* Устанавливаем высоту обратно */
        transform: translateX(0); /* Возвращаем элемент на место */
        transition: all 1s; /* Плавные переходы */
        display: flex;
        position: static;
        flex-direction: column;
        width: 320px;
        // display: flex;
        // max-width: 320px;
        // position: fixed; /* Измените на fixed, чтобы модалка оставалась на экране при прокрутке */
        // top: 0;
        // right: -550px; /* Начальное положение вне экрана */
        // height: auto; /* Устанавливаем высоту обратно */
        // transition: right 1s ease; /* Плавный переход для свойства right */
        // visibility: visible; 
        background-color: $menu-background-color;
        .menu__container-modal {
            height: 100vh;
            display: flex;
            flex-direction: column;
            position: fixed;
            width: 320px;
            .menu__header {
                padding: 30px 22px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .menu__button_close {
                    display: none;
                }
                .menu__button_close-open {
                    div {

                        background-image: url(..//assets//images/icon/ic_burger_close.png);
                    }
                    display: block;
                }
                .menu__logo {
                    background-image: url(../assets/images/logo.png)
                }
                .menu__button_search {
                    background-image: url(../assets/images/icon/ic_search.png)
                }
            }
            .menu__items {
                flex: 1 0 auto;
                padding: 15px 24px;
                display: flex;
                flex-direction: column;
                gap: 29px;
                a {
                    color: #1B1C21;
                    font-size: 16px;
                    line-height: 24px;
                    letter-spacing: -0.2px;
                }
                .menu__item-action,
                .menu__item:hover {
                    a {
                        color: #7E7E82;
                        position: relative;
                        
                        &::before {
                            content: "";
                            display: block;
                            background-color: rgb(65, 246, 215);
                            width: 4px;
                            height: 32px;
                            position: absolute;
                            border-radius: 0 5px 5px 0;
                            left: -24px;
                            top: -6px;
                        }
                    }
                }
            }
        
            .menu__footer {
                display: flex;
                flex-direction: column;
                gap: 16px;
                padding: 15px 30px;
                .menu__buttons {
                    display: flex;
                    gap: 16px;
                    .menu__button {
                        &:first-child {
                            a {
                                img {
                                    &:hover{
                                        transform: rotate(-30deg);
                                    }
                                }
                            }
                        }
                    }
                    .order-call {
                        background-image: url(../assets/images/icon/ic_phone.png)
                    }
                    .feedback {
                        background-image: url(../assets/images/icon/ic_chat.png)
                    }
                    .profile {
                        background-image: url(../assets/images/icon/ic_profile.png)
                    }
                }
                .menu__email {
                    font-size: 16px;
                    font-weight: 500;
                    line-height: 24px;
                    letter-spacing: -0.2px;
                }
                .menu__phone-number {
                    font-family: 'TT Lakes Bold', sans-serif;
                    font-size: 24px;
                    font-weight: 700;
                    line-height: 32px;
                    letter-spacing: -0.6px;
                }
                .menu__language-selection__items {
                    display: flex;
                    gap: 7px;
                    .menu__language-selection__item-activ {
                        a {
                            color: $grey-text;
                            font-weight: 600;
                        }
                    }
                    a {
                        text-decoration: none;
                        color: $black-text;
                        font-size: 16px;
                        font-weight: 500;
                        line-height: 24px;
                        letter-spacing: -0.2px;

                        &:hover {
                            color: $grey-text;
                            font-weight: 600;
                        }
                    }
                }
            }
        }
    }
    .menu-opacity {
        pointer-events: none;
        opacity: 0.1;
    }

    .page {
        .main {
            max-width: 1120px;
            margin: 0 auto;
            .main__container {
                padding-top: 42px;
                .main__header {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    position: relative;
                    .main__title {
                        font-family: 'TT Lakes Medium', sans-serif;
                        color: $black-text;
                        font-size: 28px;
                        font-weight: 700;
                        line-height: 40px;
                        letter-spacing: -0.6px;
                        &::before {
                            content: "";
                            display: block;
                            background-color: rgb(65, 246, 215);
                            width: 4px;
                            height: 40px;
                            position: absolute;
                            border-radius: 0 5px 5px 0;
                            left: -32px;
                        }
                    }
                    .main__buttons {
                        display: flex;
                        gap: 32px;
                        .main__button {
                            display: flex;
                            align-items: center;
                            gap: 8px;
                            .main__button_text {
                                color: $black-text;
                                font-size: 16px;
                                line-height: 24px;
                                letter-spacing: -0.2px;
                                transition: 0.5s;
                            }
                            .repair {
                                background-image: url(..//assets//images/icon/ic_repair.png);
                                transition: 0.5s;
                            }
                            .checkstatus {
                                background-image: url(..//assets//images/icon/ic_repair.png);
                                transition: 0.5s;
                            }
                            &:hover {
                                .main__button_text {
                                    scale: 1.03;
                                }
                                .main__button_icon {
                                    filter: drop-shadow(0px 0px 5px $pink);
                                        scale: 1.1;
                                }
                            }
                        }
                    }
                }
                .main__nav {
                    display: flex;
                    flex-wrap: wrap;
                    padding: 8px 0;
                    justify-content: space-between;
                    .main__nav_item {
                        padding: 9px 20px;
                        border: 2px solid $main-background-color;
                        a {
                            color: $grey-text;
                            font-size: 16px;
                            line-height: 24px;
                            letter-spacing: -0.2px;
                        }
                    }
                    .main__nav_item-activ,
                    .main__nav_item:hover {
                        box-sizing: border-box;
                        border: 2px solid rgb(184, 255, 236);
                        border-radius: 6px;
                        background: rgb(255, 255, 255);
                    }
                }
                .main__info {
                    display: flex;
                    justify-content: space-between;
                    gap: 20px;
                    .main__info_text {
                        max-width: 490px;
                        font-family: "TT Lakes Regular", sans-serif;
                        color: $black-text;
                        font-size: 14px;
                        line-height: 20px;
                        letter-spacing: 0.2px;
                    }
                    .main__info_text-more {
                        display: none;
                    }
                    .main__info_button-img {
                        transform: rotate(0deg);
                    }
                    .main__info_img {
                        width: 100%;
                        height: 100%;
                        div {
                            background-size: cover;
                            background-image: url(../assets/images/cps.png);
                            width: 100%;
                            height: 307px;
                            background-repeat: no-repeat;
                            background-position: center;
                        }
                    }
                }
            }
        }

        .brands {
            max-width: 1120px;
            margin: 0 auto;
            .brands__container {
                .brands__items {
                    display: grid;
                    grid-template-columns: repeat(4, 1fr);
                    row-gap: 16px;
                    column-gap: 32px;
                    .brands__item {
                            transition: 0.5s;
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            border: 1px solid $border-color;
                            border-radius: 6px;
                            background: $menu-background-color;
                            cursor: pointer;
                            &:hover {
                                border: 1px solid rgb(65, 246, 215);;
                                scale: 1.02;
                            }
                            .brands__item__logo {
                                display: flex;
                                margin-left: 16px;
                            }
                            .brands__item__button {
                                margin-right: 16px;
                                padding: 16px 0;
                                div {
                                    box-sizing: border-box;
                                    width: 40px;
                                    height: 40px;
                                    border: 2px solid $pink;
                                    border-radius: 20px;
                                    background: $menu-background-color;
                                    background-image: url(../assets/images/icon/dropdown.png);
                                    background-size: auto;
                                    background-repeat: no-repeat;
                                    background-position: center;
                                }
                        }
                        &:first-child {
                            .brands__item__logo {
                                background-image: url(../assets/images/brands/lenovo.png); 
                            }
                        }
                        &:nth-child(2) {
                            .brands__item__logo {
                                background-image: url(../assets/images/brands/samsung.png);
                            }
                        }
                        &:nth-child(3) {
                            .brands__item__logo {
                                background-image: url(../assets/images/brands/apple.png);
                            }
                        }
                        &:nth-child(4) {
                            .brands__item__logo {
                                background-image: url(../assets/images/brands/ViewSonic.png);
                            }
                        }
                        &:nth-child(5) {
                            .brands__item__logo {
                                background-image: url(../assets/images/brands/bosch.png);
                            }
                        }
                        &:nth-child(6) {
                            .brands__item__logo {
                                background-image: url(../assets/images/brands/HP.png);
                            }
                        }
                        &:nth-child(7) {
                            .brands__item__logo {
                                background-image: url(../assets/images/brands/accer.png);
                            }
                        }
                        &:nth-child(8) {
                            .brands__item__logo {
                                background-image: url(../assets/images/brands/sony.png);
                            }
                        }
                        &:nth-child(9) {
                            .brands__item__logo {
                                background-image: url(../assets/images/brands/lenovo.png);
                            }
                        }
                        &:nth-child(10) {
                            .brands__item__logo {
                                background-image: url(../assets/images/brands/samsung.png);
                            }
                        }
                        &:nth-child(11) {
                            .brands__item__logo {
                                background-image: url(../assets/images/brands/apple.png);
                            }
                        }
                    }
                    .brands-hidden {
                        display: none;
                    }
                }
                .swiper-pagination {
                    display: none;
                }
            }
        }

        .techniques {
            max-width: 1120px;
            margin: 0 auto;
            .techniques__container {
                .techniques__items {
                    display: grid;
                    grid-template-columns: repeat(4, 1fr);
                    row-gap: 16px;
                    column-gap: 32px;
                    .techniques__item {
                        display: flex;
                        height: 160px;
                        a {
                            width: 100%;
                            transition: 0.5s;
                            padding: 16px 16px;
                            display: flex;
                            justify-content: space-between;
                            border-radius: 6px;
                            border: 1px solid $border-color;
                            background: $menu-background-color;
                            cursor: pointer;
                            &:hover {
                                border: 1px solid rgb(65, 246, 215);;
                                scale: 1.02;
                            }
                            .techniques__item__text {
                                color: $black-text;
                                font-size: 16px;
                                font-weight: 500;
                                line-height: 24px;
                                letter-spacing: -0.2px;
                            }
                            .techniques__item__button {
                                margin-top: auto;
                                box-sizing: border-box;
                                width: 40px;
                                height: 40px;
                                border: 2px solid $pink;
                                border-radius: 20px;
                                background: $menu-background-color;
                                background-image: url(../assets/images/icon/dropdown.png);
                                background-size: auto;
                                background-repeat: no-repeat;
                                background-position: center;
                            }
                        }
                    }
                    .techniques-hidden {
                        display: none;
                    }
                }
                .swiper-pagination2 {
                    display: none;
                }
            }
        }

        .price {
            max-width: 1120px;
            margin: 0 auto;
            .price__container {
                .price__title {
                    display: flex;
                    width: 100%;
                    flex-wrap: wrap;
                }
                .price__title_items {
                    display: flex;
                    width: 100%;
                    padding: 7px 0px 16px;
                    margin: 0 24px;
                    .price__title_item {
                        display: flex;
                        width: 100%;
                        background-color: $main-background-color;
                        color: $grey-text;
                        font-size: 14px;
                        font-weight: 500;
                        line-height: 16px;
                        opacity: 0.72;
                        &:first-child {
                            flex: 1;
                        }
                        &:nth-child(2) {
                            flex: 0 0 230px
                        }
                        &:nth-child(3) {
                            flex: 0 0 200px
                        }
                        &:nth-child(4) {
                            flex: 0 0 136px
                        }
                    }
                }            
                .price__items {
                    display: flex;
                    flex-wrap: wrap;
                    background-color: $menu-background-color;
                    border-radius: 6px;
                    .price__item {
                        display: flex; 
                        align-items: center;
                        width: 100%; 
                        color: rgb(27, 28, 33);
                        font-family: "TT Lakes Regular", sans-serif;
                        font-size: 14px;
                        line-height: 18px;
                        letter-spacing: 0.2px;
                        padding: 23px 0;
                        margin: 0 24px;
                        border-bottom: 1px solid #EAEAEA;
                        &:last-child {
                            border-bottom: 1px solid $menu-background-color;

                        }
                        .price__item_item {

                            &:first-child {
                                flex: 1;
                            }
                            &:nth-child(2) {
                                flex: 0 0 230px
                            }
                            &:nth-child(3) {
                                flex: 0 0 200px
                            }
                            &:nth-child(4) {
                                flex: 0 0 136px
                            }
                            button {
                                display: flex;
                                align-items: center;
                                cursor: pointer;
                                border-radius: 16px;
                                background: $pink;
                                border: none;
                                transition: 1s;
                                &:hover {
                                    transition: 1s;
                                    background: $menu-background-color;
                                    .button_text {
                                        transition: 1s;
                                        color : $pink;
                                    }
                                    .button_img-arrow {
                                        transition: 1s;
                                        background-image: url(../assets/images/icon/dropdown.png);
                                        background-size: auto;
                                        width: 10px;
                                        height: 12px;
                                        background-repeat: no-repeat;
                                    }

                                }
                                .button_text {
                                    color: rgb(255, 255, 255);
                                    font-family: 'TT Lakes Bold', sans-serif;
                                    font-size: 12px;
                                    line-height: 24px;
                                    letter-spacing: -0.15px;
                                    text-transform: uppercase;
                                    padding: 4px 12px;
                                }
                                .button_img-arrow {
                                    background-image: url(../assets/images/icon/dropdown-white.png);
                                    background-size: auto;
                                    width: 8px;
                                    height: 10px;
                                    background-repeat: no-repeat;
                                    padding-left: 12px;
                                }
                            }
                        }
                    }
                }
                .price__info {
                    color: $black-text;
                    font-family: 'TT Lakes Regular', sans-serif;
                    font-size: 14px;
                    line-height: 20px;
                    letter-spacing: 0.2px;
                    padding: 32px 0 16px 0;
                    max-width: 420px;
                }
                .price__button-more {
                    transition: 1s;
                    &:hover {
                        transition: 1s;
                        color: #7E7E82;
                        font-size: 18px;
                        .price__more-button-arrow {
                            transition: 1s;
                            background-image: url(../assets/images/icon/Line.png);
                            background-size: auto;
                            width: 30px;
                            height: 10px;
                        }
                    }
                    display: flex;
                    gap: 10px;
                    align-items: center;
                    color: $black-text;
                    font-size: 16px;
                    font-weight: 500;
                    line-height: 24px;
                    letter-spacing: -0.2px;
                    .price__more-button-arrow {
                        background-image: url(../assets/images/icon/Line.png);
                        background-size: auto;
                        width: 6px;
                        height: 10px;
                    }
                }
            }
        }
        .price320 {
            display: none;
        }

        .footer {
            max-width: 1120px;
            margin: 0 auto;
            padding-top: 35px;
            .footer__container {
                display: flex;
                justify-content: space-between;
                color: $grey-text;
                font-size: 14px;
                font-weight: 500;
                line-height: 16px;
                opacity: 0.72;
                .footer__info {
                    max-width: 330px;
                    text-align: right;
                }
            }
        }

    }
}

