@import 'common.scss';

@media (max-width: 768px) {
    .modal__feedback{
        .modal__feedback__container{
            .modal__feedback__button-close{
                position: relative;
                left: 0;
                margin-bottom: 40px;
                
            }
        }
    }
    .modal__phone{
        .modal__phone__container{
            .modal__phone__button-close{
                position: relative;
                left: 0;
                margin-bottom: 40px;
            }
        }
    }
    .content {
        .page {
            .main {
                .main__container {
                    // padding-top: 24px;
                    .main__info {
                        .main__info_text {
                            max-width: 312px;
                        }
                        .main__info_img {
                            width: 360px;
                            height: 208px;
                            div {
                                background-size: contain;
                                width: 100%;
                                height: 100%;
                            }
                        }
                    }
                }
            }
        }
    }

    
}

