@import 'common.scss';

@media (max-width: 1119px) {

    .content {
        display: block;
        .page {

            .main {
                .main__container {
                    padding-top: 24px;
                    .main__header {
                        .main__buttons {
                            display: none;
                        }
                    }
                    .main__nav {
                        flex-wrap: nowrap;    
                        overflow-x: scroll;
                        white-space: nowrap;
                        margin: 32px 0;
                    }
                }
            }
    
        }
    }
}