@import 'common.scss';


@media (max-width: 1439px) {

    .header {
        opacity: 1; /* Делаем элемент видимым */
        visibility: visible; /* Делаем элемент видимым для взаимодействия */
        height: auto; /* Устанавливаем высоту обратно */
        transform: translateY(0); /* Возвращаем элемент на место */
    }

    .content {
        flex-wrap: wrap;
        .menu {
            position: absolute;
            left: 0;
            opacity: 0; /* Скрываем элемент */
            visibility: hidden; /* Делаем элемент невидимым для взаимодействия */
            height: 0; /* Убираем занимаемое место */
            transform: translateX(-100%); /* Сдвигаем элемент вверх */
            transition: all 1s; /* Плавные переходы */
            background-color: $menu-background-color;
        }
        .active {
            opacity: 1;
            visibility: visible;
            height: auto;
            transform: translateX(0);
            top: 0;
        }

    }



}
