@import 'common.scss';


.modal__phone{
    display: flex;
    max-width: 440px;
    position: fixed; /* Измените на fixed, чтобы модалка оставалась на экране при прокрутке */
    top: 0;
    right: -550px; /* Начальное положение вне экрана */
    height: auto; /* Устанавливаем высоту обратно */
    transition: right 1s ease; /* Плавный переход для свойства right */
    visibility: visible; 
    
    .modal__phone__container{
        position: relative;
        padding: 40px 32px;
        height: 100vh;
        background-color: $menu-background-color;
        .modal__phone__button-close{
            position: absolute;
            left: -64px;
            div {

                background-image: url(../assets/images/icon/ic_burger_close.png)
            }
        }
        .modal__phone__title{
            position: relative;
            color: rgb(27, 28, 33);
            font-family: "TT Lakes Bold", sans-serif;
            font-size: 24px;
            font-weight: 700;
            line-height: 32px;
            letter-spacing: -0.6px;
            text-align: left;
            &::before {
                content: "";
                display: block;
                background-color: rgb(65, 246, 215);
                width: 4px;
                height: 40px;
                position: absolute;
                border-radius: 0 5px 5px 0;
                left: -32px;
                top: -6px;
            }

        }
        .modal__phone__form{
            margin-top: 24px;
            display: flex;
            flex-direction: column;
            input {
                border: 1px solid rgb(234, 234, 234);
                border-radius: 8px;
                background: rgb(255, 255, 255);
                padding: 12px 16px;
            }
            
        }
        .modal__phone__text{
            margin-top: 14px;
            font-size: 14px;
            font-weight: 500;
            line-height: 16px;
            span {
                color: rgb(255, 62, 121);
            }
        }
        .modal__phone__button{
            margin: 14px 0 0 auto;
            // margin-top: 14px;
            display: flex;
            align-items: center;
            cursor: pointer;
            border-radius: 16px;
            background: $pink;
            border: none;
            transition: 1s;
            &:hover {
                transition: 1s;
                background: $menu-background-color;
                .button__phone__button-text {
                    transition: 1s;
                    color : $pink;
                }
                .button__phone__button-img {
                    transition: 1s;
                    background-image: url(../assets/images/icon/dropdown.png);
                    background-size: auto;
                    width: 10px;
                    height: 12px;
                    background-repeat: no-repeat;
                }

            }
            .button__phone__button-text {
                color: rgb(255, 255, 255);
                font-family: 'TT Lakes Bold', sans-serif;
                font-size: 12px;
                line-height: 24px;
                letter-spacing: -0.15px;
                text-transform: uppercase;
                padding: 4px 12px;
            }
            .button__phone__button-img {
                background-image: url(../assets/images/icon/dropdown-white.png);
                background-size: auto;
                width: 8px;
                height: 10px;
                background-repeat: no-repeat;
                padding-left: 12px;
            }
        }
    }
}


.modal__phone-open {
    right: 0; /* Перемещаем модалку на экран */
    visibility: visible; /* Делаем элемент видимым */
    z-index: 5;


    .modal__phone__container-open {
        right: 0;
        background-color: $menu-background-color;  
        box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2);
    }
}



