@import 'common.scss';

@media (max-width: 740px) {
    
    .content {
        .page {

            .main {
                .main__container {
                    padding: 24px 16px;
                    .main__header {
                        .main__title {
                            &::before {
                                left: -16px;
                            }
                        }
                    }
                    .main__nav {
                        margin: 24px 0;
                    }
                    .main__info {
                        // flex-direction: column;
                        .main__info_text {
                            // max-width: 100%;
                            .media320 {
                                display: none;
                            }
                        }
                        .main__info_img {
                            text-align: center;
                        }
                    }
                }
            }
    
            .brands {
                .brands__container {
                    .brands__items {
                        grid-template-columns: repeat(2, 1fr);
                        row-gap: 16px;
                        column-gap: 24px;
                    }
                }
            }
    
            .techniques {
                .techniques__container {
                    .techniques__items {
                        grid-template-columns: repeat(2, 1fr);
                        column-gap: 24px;
                    }
                }
            }
    
            .footer {
                .footer__container {
                    padding: 24px 16px;
                    flex-direction: column;
                    gap: 16px;
                    text-align: center;
                    .footer__info {
                        text-align: center;
                        max-width: 600px;
                    }
                }
            }
    
        }
    }
}
