@import 'common.scss';

@media (max-width: 999px) {
    .content {
        .page {

            .main {
                .main__container {
                    .main__info {
                        display: flex;
                        justify-content: space-between;
                        gap: 20px;
                        .main__info_text {
                            max-width: 350px;
                        }
                    }
                }
            }
    
            .brands {
                .brands__container {
                    .brands__items {
                        grid-template-columns: repeat(3, 1fr);
                        row-gap: 16px;
                        column-gap: 24px;
                    }
                }
            }
    
            .techniques {
                .techniques__container {
                    .techniques__items {
                        grid-template-columns: repeat(3, 1fr);
                        column-gap: 24px;
                    }
                }
            }
    
        }
    }
}