@import 'common.scss';

@media (max-width: 930px) {
    .content {
        .page {

            .main {
                .main__container {
                    .main__info {
                        .main__info_text {
                            // max-width: 520px;
                            p {
                                span {
                                    display: none;
                                }
                            }
                            p:first-child {
                                margin: 0;
                            }
                            p:nth-child(2),
                            p:last-child {
                                margin: 20px 0 0 0;
                            }
                        }
                        .main__info_button {
                            padding-top: 12px;
                        }
                    }
                }
            }
    
            .footer {
                .footer__container {
                    .footer__info {
                        max-width: 213px;
                    }
                }
            }
    
        }
    }
}