@import 'common.scss';

@media (max-width: 539px) {
    
    .header {
        .header__menu {
            padding: 24px 16px;
            .header__burger-logo {
                gap: 16px;
                .header__line {
                    display: none;
                }
            }
            .header__action {
                gap: 16px;
                .header__button {
                    display: none;
                }
            }
        }
    }

    .content {
        .page {

            .title {
                .title__container {
                    .title_info {
                        padding-left: 16px;
                        padding-right: 16px;
                    }
                }
            }
                
            .brands {
                .brands__container {
                    display: flex;
                    flex-direction: column;
                    padding: 16px 16px;
                    overflow: hidden;
    
                    .brands__items {
                        display: flex;
                        column-gap: 0;
                        .brands__item {
                            padding: 0;
                            .brands__item__logo {
                                margin-left: 16px;
                                background-size: auto;
                                height: 72px;
                            }
                            .brands__item__button {
                                padding-right: 16px;
                            }
                        }
                    }
                    .swiper-pagination {
                        display: flex;
                    }
                    .brands__info_button {
                        display: none;
                    }
                }
            }
    
            .techniques {
                display: flex;
                flex-direction: column;
                .techniques__container {
                    overflow: hidden;
                    padding: 16px 16px;
                    .techniques__items {
                        display: flex;
                        column-gap: 0;
                    }
                    .swiper-pagination2 {
                        display: flex;
                    }
                    .techniques__info_button {
                        display: none;
                    }
                }
            }
    
            .price {
                display: none;
            }
    
            .price320{
                display: flex;
                flex-direction: column;
                .price320__container {
                    padding: 16px 16px;
                    overflow: hidden;
                    font-size: 14px;
                    .price320__items {
                        .price320__item {
                            position: relative;
                            box-sizing: border-box;
                            height: 200px;
                            padding: 16px 16px;
                            border: 1px solid rgb(234, 234, 234);
                            background-color: $menu-background-color;
                            border-radius: 6px;
                            font-size: 14px;
                            .price320__item-price,
                            .price320__item-term {
                                padding-top: 16px;
                            }
                            .price320__item-name {
                                display: flex;
                                opacity: 0.72;
                                color: $grey-text;
                                line-height: 16px;
                            }
                            .price320__item-service {
                                display: flex;
                                color: $black-text;
                                font-family: "TT Lakes Regular", sans-serif;
                                line-height: 18px;
                                letter-spacing: 0.2px;
                            }
                            .price320__item-button {
                                button {
                                    position: absolute;
                                    cursor: pointer;
                                    bottom: 16px;
                                    right: 16px;
                                    display: flex;
                                    align-items: center;
                                    border-radius: 16px;
                                    background-color: $pink;
                                    border: none;
                                    .button_text {
                                        color: rgb(255, 255, 255);
                                        font-family: 'TT Lakes Bold', sans-serif;
                                        font-size: 12px;
                                        line-height: 24px;
                                        letter-spacing: -0.15px;
                                        text-transform: uppercase;
                                        padding: 4px 10px;
                                    }
                                    .button_img-arrow {
                                        background-image: url(../assets/images/icon/dropdown-white.png);
                                        background-size: auto;
                                        width: 8px;
                                        height: 10px;
                                        background-repeat: no-repeat;
                                        padding-left: 8px;
                                    }
                                }
                            }
    
                        }
                        
                    }
                    .price__info {
                        color: $black-text;
                        font-family: 'TT Lakes Regular', sans-serif;
                        font-size: 14px;
                        line-height: 20px;
                        letter-spacing: 0.2px;
                        padding: 32px 0 16px 0;
                        max-width: 420px;
                    }
                    .price__button-more {
                        transition: 1s;
                        &:hover {
                            transition: 1s;
                            color: #7E7E82;
                            font-size: 18px;
                            // .price__more-button-arrow {
                            //     transition: 1s;
                            //     background-image: url(../assets/images/icon/Line.png);
                            //     background-size: auto;
                            //     width: 30px;
                            //     height: 10px;
                            // }
                        }
                        display: flex;
                        gap: 10px;
                        align-items: center;
                        color: $black-text;
                        font-size: 16px;
                        font-weight: 500;
                        line-height: 24px;
                        letter-spacing: -0.2px;
                        // .price__more-button-arrow {
                        //     background-image: url(../assets/images/icon/Line.png);
                        //     background-size: auto;
                        //     width: 6px;
                        //     height: 10px;
                        // }
                    }
                }
            }
    
        }
    }


}

