@import '~normalize.css';
a {
  cursor: pointer;
  text-decoration: none;
}

.line {
  width: 2px;
  height: 32px;
  background: rgb(242, 242, 242);
}

.logo a div {
  background-image: url(../assets/images/logo.png);
  background-size: auto;
  background-repeat: no-repeat;
  width: 97px;
  height: 51px;
  transition: 0.5s;
}
.logo a div:hover {
  filter: drop-shadow(0px 0px 1px #80949F);
  scale: 1.05;
}

.button-icon a div {
  width: 40px;
  height: 40px;
  background-size: auto;
  background-repeat: no-repeat;
  transition: 0.5s;
}
.button-icon a :hover {
  transition: 0.5s;
  filter: drop-shadow(0px 0px 5px rgb(255, 62, 121));
  scale: 1.1;
}

.container {
  padding: 24px 32px;
  border-bottom: 1px solid rgb(217, 255, 245);
}

.button-more {
  font-family: "TT Lakes Medium", sans-serif;
  padding-top: 24px;
  display: flex;
  align-items: center;
  transition: 0.5s;
  color: rgb(27, 28, 33);
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
}
.button-more .button-more_img {
  background-image: url(../assets/images/icon/icon_arrow.png);
  background-size: auto;
  background-repeat: no-repeat;
  background-position: center;
  width: 8px;
  height: 12px;
  padding: 16px;
}
.button-more:hover {
  opacity: 0.5;
}

.title {
  font-family: "TTLakes-Regular", sans-serif;
  color: rgb(126, 126, 130);
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-transform: uppercase;
  background: linear-gradient(270deg, rgb(255, 255, 255), rgb(248, 248, 248) 98.614%);
}
.title .title__container {
  margin: 0 auto;
  max-width: 1120px;
}
.title .title__container .title_info {
  padding: 16px 32px;
}

.brands__item__logo {
  background-size: auto;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center left;
}

.swiper-pagination,
.swiper-pagination3,
.swiper-pagination2 {
  display: flex;
  justify-content: center;
  position: relative;
  padding-top: 16px;
}

.swiper-pagination-bullet {
  background: rgb(221, 221, 221);
  width: 12px;
  height: 12px;
  margin: 0 6px;
}

.swiper-pagination-bullet-active {
  background: rgb(181, 182, 188);
}

.no-scroll {
  overflow: hidden;
}

.menu-open .menu__container-modal {
  background-color: #FFF;
  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2);
}

.dn {
  display: none;
}

.menu-opacity,
.page-opacity,
.header-opacity {
  pointer-events: none;
  opacity: 0.1;
}